.navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    background-color: #333;
    color: white;
    padding: 10px 20px;
  }
  
  .navbar-left h2 {
    margin: 0;
    font-size: 24px;
  }
  
  .navbar-left ul {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
  }
  
  .navbar-left ul li {
    margin-right: 20px;
    font-size: 18px;
    cursor: pointer;
  }
  
  .navbar-right {
    font-size: 18px;
  }
  